.gn-action-bar-item {
  list-style: none;
  margin: 0;
  padding: 0;
}
.gn-action-bar-item_pull_left {
  margin-left: 0;
  margin-right: auto;
}
.gn-action-bar-item_pull_right {
  margin-left: auto;
  margin-right: 0;
}
.gn-action-bar-item_pull_center {
  margin-left: auto;
  margin-right: auto;
}
.gn-action-bar-item + .gn-action-bar-item_spacing {
  margin-left: 8px;
}